.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .input-container {
    display: flex;
    gap: 15px;
    margin-bottom: 15px;
  }
  
  input {
    padding: 5px 8px;
    width: 559px;  /* Adjust width to make it larger */
    border-radius: 50px;  /* Oval shape */
    border: 2px solid #ccc;
    font-size: 12px;  /* Larger text */
  }
  
  button {
    padding: 15px 30px;
    border-radius: 50px;
    background-color: #42a5f5;
    color: white;
    border: none;
    font-size: 18px;  /* Larger button text */
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #1e88e5;
  }
  
  